@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap);
*{
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  background-color: #f3f2ef;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.app-body {
  display: flex;
  width: 90%;
  margin-top: 20px;
  max-width: 1200px;
  margin-left: 20px;
  margin-right: 20px;
}

.feed {
  flex: 0.6 1;
  margin: 0 20px;
}

.feed-inputContainer {
  background-color: white;
  padding: 10px;
  padding-bottom: 20px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.feed-input {
  border: 1px solid lightgrey;
  border-radius: 30px;
  display: flex;
  padding: 10px;
  color: grey;
  padding-left: 15px;
}

.feed-input > form {
  display: flex;
  width: 100%;
}

.feed-input > form > input {
  border: none;
  flex: 1 1;
  margin-left: 10px;
  outline-width: 0;
  font-weight: 600;
}

.feed-input > form > button {
  display: none;
}

.feed-inputOptions {
  display: flex;
  justify-content: space-evenly;
}

.inputOption {
  display: flex;
  align-items: center;
  margin-top: 15px;
  color: grey;
  padding: 10px;
  cursor: pointer;
}

.inputOption:hover {
  background-color: whitesmoke;
  border-radius: 10px;
}

.inputOption > h4 {
  margin-left: 5px;
}

.post {
  background-color: white;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.post-header {
  display: flex;
  margin-bottom: 10px;
}

.post-info {
  margin-left: 10px;
}

.post-info > p {
  font-size: 12px;
  color: grey;
}

.post-info > h2 {
  font-size: 15px;
}

.post-body {
  overflow-wrap: unset;
}

.post-buttons {
  font-size: 12px;
  display: flex;
  justify-content: space-evenly;
}

.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.1px solid lightgrey;
  background-color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 5px;
  width: 100%;
  z-index: 999;
}

.header-left {
  display: flex;
}

.header-left > img {
  object-fit: contain;
  height: 39px;
  margin-right: 10px;
}

.header-search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  height: 20px;
  color: grey;
  background-color: #eef3f8;
}

.header-search > input {
  padding-left: 3px;
  outline: none;
  border: none;
  background: none;
}

.header-right {
  display: flex;
}

.headerOption{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color: grey;
    cursor: pointer;
}

.headerOption:hover{
    color: black;
}

.headerOption-title{
    font-size: 13px;
    font-weight: 400;
}

.headerOption-icon{
    object-fit: contain;
    height: 25px !important;
    width: 25px !important;;
}
.login {
  display: grid;
  place-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 80px;
  padding-bottom: 100px;
}

.login > img {
  object-fit: contain;
  height: 90px;
  margin-top: 20px;
  margin-bottom: -5px;
}

.login > form {
  display: flex;
  flex-direction: column;
}

.login > form > input {
  width: 350px;
  height: 25px;
  font-size: 15px;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.login > form > button {
  width: 372px;
  height: 35px;
  padding: 10px;
  font-size: 15px;
  color: #fff;
  background-color: #0074b1;
  border-radius: 5px;
  cursor: pointer;
}

.login > p {
  margin-top: 15px;
}

.login-register {
  color: #0177b7;
  cursor: pointer;
}

.sidebar {
  position: sticky;
  top: 80px;
  flex: 0.2 1;
  border-radius: 10px;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.sidebar-avatar {
  margin-bottom: 10px;
}

.sidebar-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgrey;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
  font-family: "Noto Sans JP", sans-serif;
}

.sidebar-top > h4 {
  color: grey;
  font-size: 12px;
}

.sidebar-top > h2 {
  font-size: 18px;
}

.sidebar-top > img {
  width: 100%;
  height: 60px;
  margin-bottom: -20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}

.sidebar-stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgrey;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sidebar-stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar-stat > p {
  color: grey;
  font-size: 13px;
  font-weight: 600;
}

.sidebar-statNumber {
  font-weight: bold;
  color: #0a66c2 !important;
}

.sidebar-bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgrey;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}

.sidebar-bottom > p {
  font-size: 13px;
  padding-bottom: 10px;
}

.sidebar-recentItem {
  display: flex;
  font: 13px;
  color: grey;
  font-weight: bolder;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}

.sidebar-recentItem:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}

.sidebar-hash {
  margin-right: 10px;
  margin-left: 5px;
}

.widgets {
  top: 80px;
  position: sticky;
  flex: 0.2 1;
  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 10px;
}

.widgets-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.widgets-header > h2 {
  font-size: 16px;
  font-weight: 400;
}

.widgets-article {
  display: flex;
  padding: 10px;
  cursor: pointer;
}

.widgets-article:hover {
  background-color: whitesmoke;
}

.widgets-articleLeft {
  color: #0177b7;
  margin-right: 5px;
}

.widgets-articleLeft > .MuiSvgIcon-root {
  font-size: 15px;
}

.widgets-articleRight {
  flex: 1 1;
}

.widgets-articleRight > h4 {
  font-size: 14px;
}

.widgets-articleRight > p {
  font-size: 12px;
  color: gray;
}

